export default{
	data(){
		return {
			type:'',
			platform:'',
			loading: false,
			price: 1,
			providerList: []
		}
	},
	mounted(){
		this.platform=uni.getSystemInfoSync().platform
		this.getProviders()
	},
	methods:{
		to(w){
			
			// uni.navigateTo({
			// 	url:'./payResult'
			// })
		},
		radioChange(e){
			this.type=e.detail.value
			// if(this.type=='wxpay'){
			// 	this.weixinPay()
			// }else{
				this.requestPayment(this.type)
			// }
		},
		getProviders(){










































		},
		weixinPay() {
		    console.log("发起支付");
		    this.loading = true;
		    uni.login({
		        success: (e) => {
		            console.log("login success", e);
		            uni.request({
		                url: `https://unidemo.dcloud.net.cn/payment/wx/mp?code=${e.code}&amount=${this.price}`,
		                success: (res) => {
		                    console.log("pay request success", res);
		                    if (res.statusCode !== 200) {
		                        uni.showModal({
		                            content: "支付失败，请重试！",
		                            showCancel: false
		                        });
		                        return;
		                    }
		                    if (res.data.ret === 0) {
		                        console.log("得到接口prepay_id", res.data.payment);
		                        let paymentData = res.data.payment;
		                        uni.requestPayment({
		                            timeStamp: paymentData.timeStamp,
		                            nonceStr: paymentData.nonceStr,
		                            package: paymentData.package,
		                            signType: 'MD5',
		                            paySign: paymentData.paySign,
		                            success: (res) => {
		                                uni.showToast({
		                                    title: "感谢您的赞助!"
		                                })
		                            },
		                            fail: (res) => {
		                                uni.showModal({
		                                    content: "支付失败,原因为: " + res
		                                        .errMsg,
		                                    showCancel: false
		                                })
		                            },
		                            complete: () => {
		                                this.loading = false;
		                            }
		                        })
		                    } else {
		                        uni.showModal({
		                            content: res.data.desc,
		                            showCancel: false
		                        })
		                    }
		                },
		                fail: (e) => {
		                    console.log("fail", e);
		                    this.loading = false;
		                    uni.showModal({
		                        content: "支付失败,原因为: " + e.errMsg,
		                        showCancel: false
		                    })
		                }
		            })
		        },
		        fail: (e) => {
		            console.log("fail", e);
		            this.loading = false;
		            uni.showModal({
		                content: "支付失败,原因为: " + e.errMsg,
		                showCancel: false
		            })
		        }
		    })
		},
		async requestPayment(e) {
		    this.loading = true;
		    let orderInfo = await this.getOrderInfo(e);
			console.log(orderInfo)
		    console.log("得到订单信息", orderInfo);
		    if (orderInfo.statusCode !== 200) {
		        console.log("获得订单信息失败", orderInfo);
		        uni.showModal({
		            content: "获得订单信息失败",
		            showCancel: false
		        })
		        return;
		    }
		    uni.requestPayment({
		        provider: e,
		        orderInfo: orderInfo.data,
		        success: (e) => {
		            console.log("success", e);
		            uni.showToast({
		                title: "感谢您的赞助!"
		            })
		        },
		        fail: (e) => {
		            console.log("fail", e);
		            uni.showModal({
		                content: "支付失败,原因为: " + e.errMsg,
		                showCancel: false
		            })
		        },
		        complete: () => {
		            this.loading = false;
		        }
		    })
		},
		getOrderInfo(e) {
		    let appid = "";



		    let url = 'https://demo.dcloud.net.cn/payment/?payid=' + e + '&appid=' + appid + '&total=' + this.price;
		    return new Promise((res) => {
		        uni.request({
		            url: url,
		            success: (result) => {
		                res(result);
		            },
		            fail: (e) => {
		                res(e);
		            }
		        })
		    })
		},
	}
}
